<template>
    <div class="read">
      <div class="post">
        <h1>{{title}}</h1>
        <div class="post-body">
          <p style="text-align:center">Txid：{{id}} </p>
          <p style="text-align:center"><a :href="veforge" target="_blank">VerForge查询</a> </p>
          <p>{{desc}}</p>
          <p>　</p>
          <div v-html="content"></div>
        </div>
      </div>
    </div>
</template>

<script>

  import store from '@/store'
  import validator from 'validator'

  document.title = 'Info - Vechain';

  export default {
      name: "read",
      store,
      data(){
        return{
          'title':'',
          'desc':'',
          'content':''
        }
      },
      computed:{
        id(){
          return this.$route.params.id;
        },
        veforge(){
          return 'https://explore.veforge.com/transactions/' + this.$route.params.id

        }
      },
      mounted() {
        this.getdata()
      },
      methods:{
        getdata(){


          try {

            const transaction=connex.thor.transaction(this.id)
            transaction.get().then(val => {

              if( val.clauses ){

                var leng = val.clauses.length

                console.log(leng)

                if(leng >= 2){

                  var data = val.clauses[0].data.slice(2)
                  var str = new Buffer( data, 'hex').toString('utf8')
                  if(validator.isJSON(str)){
                    //json格式 -> 进行判断
                    str = JSON.parse(str)
                    if(str.hasOwnProperty("title") && str.hasOwnProperty("desc") && str.hasOwnProperty("info") && str.info === '佛经在线'){
                      //符合标准，本站提交
                      console.log(str.title)
                      this.title = str.title
                      this.desc = str.desc
                    }
                  }

                  var content = '';

                  for(var i=1;i<leng;i++){

                    content += val.clauses[i].data.slice(2)

                  }

                  //内容解密
                  str = new Buffer( content, 'hex').toString('utf8')

                  this.content = str

                }else{

                  this.title='读取失败,请检查txid是否为本站提交的数据'
                  this.content=''
                  this.desc=''
                  this.id=''

                }

              }
            })

          } catch (err) {

            this.title='读取失败,请检查txid是否正确及是否通过佛经在线提交的数据 或 与connes通信失败'
            this.content=''
            this.desc=''

          }

        }
      }
  }
</script>

